.name{
    font-weight: 500;
        font-size: 15px;
        margin-right: 10px;
}

.button{
    padding: 8px 5px;
        cursor: pointer;
        width: 100px;
        border-radius: 6px;
        color: #ffffff;
        background: linear-gradient(180deg,
                rgba(248, 110, 103, 0.63) 0%,
                rgba(253, 86, 143, 0.86) 100%);
        box-shadow: 0px 7px 27px -2px rgba(253, 88, 140, 0.35);
        border: inherit;
    
        font-family: 'Roboto';
        font-weight: 500;
        font-size: 15px;
}
        .button:hover,
        .button:focus {
            border: solid 1px #f3848c;
            color: #f3848c;
            background: #ffffff;
            filter: drop-shadow(10px 15px 25px rgba(232, 183, 183, 0.45));
        }

        .wrap{
            display: flex;
                align-items: center;
                gap: 5px;
        }